
<!--我的优惠页面-->

<template>
    <div>
        <!--红包-->
        <div v-if="explainIsShow">
            <!--头部-->
            <header id="head_top">
                <section class="head_goback" @click="goMy">
                    <i class="el-icon-arrow-left"></i>
                </section>
                <section class="title_head ellipsis">
                    <span class="title_text">
                        我的优惠
                    </span>
                </section>
            </header>
            <!---->
            <section>
                <!--红包、商家代金券-->
                <section class="category_title">
                    <span :class="isShow?'choosed':''" @click="isShow=!isShow">红包</span>
                    <span :class="!isShow?'choosed':''" @click="isShow=!isShow">商家代金券</span>
                </section>
                <!--红包详细内容-->
                <section class="content" v-if="isShow" >
                    <section class="hongbao_container">
                        <!--红包说明-->
                        <header class="hongbao_title">

                            <section class="total_number">
                                有 <span>{{redPacket.length}}</span>个红包即将到期
                            </section>
                            <section class="hongbao_description">
                                <i class="el-icon-question"></i>
                                <a @click="explainIsShow=!explainIsShow">红包说明</a>
                            </section>
                        </header>
                        <!--红包详情-->
                        <ul class="hongbao_list_ul">
                            <li class="hongbao_list_li" v-for="red in redPacket">
                                <section class="list_item">
                                    <div class="list_item_left">
                                        <span>￥</span>
                                        <span>{{red.amount==4.5?4:red.amount }}</span>
                                        <span>.</span>
                                        <span>0</span>
                                        <p>{{red.description_map.sum_condition}}</p>
                                    </div>
                                    <div class="list_item_right">
                                        <h4>{{red.name}}</h4>
                                        <p>{{red.description_map.validity_periods}} </p>
                                        <p>{{red.description_map.phone}}</p>
                                    </div>
                                    <div class="time_left">{{red.description_map.validity_delta}}</div>
                                </section>
                            </li>
                        </ul>
                        <!--限品类-->
                        <footer class="list_item_footer">
                            <p>限品类：快餐便当、特色菜系、小吃夜宵、甜品饮品、异国料理</p>
                        </footer>
                    </section>
                    <!--查看历史红包-->
                    <router-link to="/hbHistory">
                        <span class="check_history">查看历史红包</span>
                        <i class="el-icon-arrow-right"></i>
                    </router-link>
                    <!--兑换红包、推荐有奖-->
                    <footer class="hongbao_footer">
                        <router-link to="/huanhb">兑换红包</router-link>
                        <router-link to="">推荐有奖</router-link>
                    </footer>
                </section>

                <section class="voucher_container"  v-if="!isShow">
                    <section class="hongbao_description voucher_header">
                        <i class="el-icon-question"></i>
                        <router-link to="">商家代金券说明</router-link>
                    </section>
                </section>
                <section class="unable_use"  v-if="!isShow">
                    <img src="../assets/voucher.png" alt="">
                    <p>无法使用代金券</p>
                    <p>非客户端或客户端版本过低</p>
                    <router-link to="">
                        下载或升级客户端
                    </router-link>

                </section>
            </section>
        </div>
        <!--红包说明-->
        <div v-if="!explainIsShow">
            <header id="explain_top">
                <section class="head_goback" @click="explainIsShow = !explainIsShow">
                    <i class="el-icon-arrow-left"></i>
                </section>
                <section class="title_head ellipsis">
                <span class="title_text">
                    红包说明
                </span>
                </section>
            </header>
            <section class="scroll_container" id="scroll_section">
                <div class="markdown">
                    <h3>Q1: 怎么获得红包？</h3>
                    <ul>
                        <li>通过邀请好友获得</li>
                        <li>在下单返红包的商家下单获得</li>
                        <li>抢微信朋友分享的红包获得</li>
                    </ul>
                    <h3>Q2: 红包可以做什么？</h3>
                    <p>可以抵扣在线支付时的实际支付金额。</p>
                    <h3>Q3: 红包的门槛金额按照什么金额计算？</h3>
                    <p>红包的的门槛金额按照（选购菜品折后价＋餐盒费）的金额作为计算门槛，即购物车显示的金额（7.3以上版本）。假设红包满30可用，只要选购的菜品现价（折后价）＋餐盒费超过30，就可以使用红包。</p>
                    <h3>Q4: 一个红包能拆开多次使用吗？</h3>
                    <p>不能，一个红包只能一次性使用，不能分开使用。</p>
                    <h3>Q5: 下单的时候使用了红包，但是后来订单取消了，红包还会返还吗？</h3>
                    <p>会的，订单无效后红包会自动返还到您的账户里。</p>
                    <h3>Q6: 红包兑换码怎样兑换成红包，怎样查看红包？</h3>
                    <p>在个人中心 &gt; 我的红包 &gt; 兑换红包，输入兑换码进行兑换.</p>
                </div>
            </section>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Benefit",
        data(){
            return{
                isShow:true,
                user:"",
                user_id:"",
                redPacket:[],
                explainIsShow:true,
            }
        },
        created(){
            if (localStorage.getItem('user')){
                this.user = JSON.parse(localStorage.getItem('user'))
                this.user_id = this.user[0].user_id
                console.log(this.user_id);
                this.axios.get("promotion/v2/users/"+this.user_id+"/hongbaos"+'?limit=20&offset=0', {
                    user_id:this.user_id,
                    limit:20,
                    offset:0
                }).then(res=>{
                    this.redPacket = res
                    console.log(res)
                }).catch(err=>{

                })
            }

        },
        methods:{
            goMy() {
                this.$router.push({
                    path:'/profile'
                })
            },
        }
    }
</script>

